//Based on http://www.samliew.com/icval/
export default function(s) {
  if (s.length !== 9) return false;

  s = s.toUpperCase();
  let weights = [2, 7, 6, 5, 4, 3, 2];

  var total = 0;
  for (var i = 0; i < weights.length; i++) {
    total += parseInt(s[i + 1], 10) * weights[i];
  }

  var offset = s[0] === 'T' || s[0] === 'G' ? 4 : 0;
  var temp = (offset + total) % 11;

  var st = ['J', 'Z', 'I', 'H', 'G', 'F', 'E', 'D', 'C', 'B', 'A'];
  var fg = ['X', 'W', 'U', 'T', 'R', 'Q', 'P', 'N', 'M', 'L', 'K'];

  var checkAlpha;
  if (s[0] === 'S' || s[0] === 'T') {
    checkAlpha = st[temp];
  } else if (s[0] === 'F' || s[0] === 'G') {
    checkAlpha = fg[temp];
  }

  return s[8] === checkAlpha;
}
